import uniList from "@/components/uni-list/uni-list.vue"
import uniListItem from "@/components/uni-list-item/uni-list-item.vue"
	import Img2Base64 from "@/common/img2Base64.js"
import {
	mapState
} from "vuex"
export default {
	components: {
		uniList,
		uniListItem
	},
	data() {
		return {
			img: Img2Base64,
			name: 'compInput',
			behaviors: ['uni://form-field'],
			time: '7:00',
			modalName: null,
			index1: -1,
			index2: -1,
			index3: -1,
			picker2: ['清华大学', '北京大学', '中国科学技术大学', '复旦大学', '中国人民大学', '上海交通大学', '南京大学', '同济大学', '浙江大学', '南开大学'],
			picker1: ['考研', '日常'],
			testValue: 'Hello',
		}
	},
	computed: {
		...mapState({
			userinfo: state => state.userinfo,
			hasLogin: state => state.hasLogin
		})
	},
	methods: {
		updateTarget(){
			uni.request({
				url:`https://www.doaho.work:8080/user/update?salt=${this.userinfo['salt']}`,
				method:"POST",
				data:{
					"target": this.userinfo['target']
				},
				success: (res) => {
					console.info(res.data.message)
				}
			})
		},
		quit() {
			this.$store.commit('logout')
			uni.navigateBack({
				delta:1
			})
		},
		switchChange(e) {
			uni.showToast({
				title: 'change:' + e.value,
				icon: 'none'
			})
		},
		updateName() {
			uni.request({
				url: `https://www.doaho.work:8080/user/update?salt=${this.userinfo['salt']}`,
				method: "POST",
				data: {
					"nickname": this.userinfo['nickname']
				},
				success: (res) => {
					console.info(res.data.message)
				}
			})
		},
		to(name) {
			console.log('跳转');
			uni.navigateTo({
				url: name
			});
		},
		onInput(e) {
			this.$emit('input', e.detail.value)
		},
		showModal(e) {
			this.modalName = "DialogModal1"
		},
		showModal2(e) {
			this.modalName = "DialogModal2"
		},
		showModal3(e) {
			this.modalName = "DialogModal3"
		},
		hideModal(e) {
			this.modalName = null
		},
		PickerChange1(e) {
			this.index1 = e.detail.value
		},
		PickerChange2(e) {
			this.index2 = e.detail.value
		},
		PickerChange3(e) {
			this.index3 = e.detail.value
		},
		TimeChange(e) {
			this.time = e.detail.value
		},
		hideModal_submit(e) {
			console.log(12)
			this.modalName = null
		},

	}

}
